/**
 * card post list
 */
.card {
  border: none;
}

.nav-link {
  font-size: 1.3em;
}

/**
 * Site header
 */
.site-title {
  @include relative-font-size(1.625);
  font-weight: 300;
  line-height: $base-line-height * $base-font-size * 2.25;
  letter-spacing: -1px;
  margin-bottom: 0;
  float: left;

  &,
  &:visited {
    color: $city-light;
  }
}

/**
 * Site footer
 */
 footer {
  bottom:0;
  background-color: $header-footer-color;

  .container {
    margin-top: 40px;
    color: $city-light;
    margin-bottom: 60px;
    a {
         color: $dark-green;
    }
  }
}

/**
 * Page content
 */
.page-heading {
  @include relative-font-size(2);
}

.post-list-heading {
  margin-top: 20px;
  @include relative-font-size(1.75);
}

.post-list {
  margin-left: 0;
  margin-top: 15px;
  list-style: none;
  display: flex;
  border-radius: $border-radius;
}

.post-meta {
  font-size: 30px;
  color: $brand-color;
}

.post-meta-span {
  color: $white;
}

// JAVA
.post-link {
  display: block;
  padding: 10px;
  padding-left: 20px;
  padding-right: 20px;
  border: 2px solid $java-color;
  border-radius: $border-radius;
  background-color: $java-color;
  width: 100%;
  @include relative-font-size(1.25);
  h4 {
    color: $tag-h3-color;
  }
}

.post-link:hover {
  text-decoration: none;
  border: 2px solid $soothing-breeze;
  .post-meta-span {
    color:  $white;
  }
  > h4 {
    color: $tag-color-hover;
  }
}


