/**
 * Posts
 */

 .post {
   margin-top: 40px;
   margin-bottom: 80px;
 }

.post-header {
  margin-bottom: $spacing-unit;
}

.post-title {
  @include relative-font-size(2.625);
  letter-spacing: -1px;
  line-height: 1;
  color: $dark-green;

  @include media-query($on-laptop) {
    @include relative-font-size(2.25);
  }
}

.post-content {
  margin-bottom: $spacing-unit;

  h2 {
    @include relative-font-size(2);
    color: $dark-green;

    @include media-query($on-laptop) {
      @include relative-font-size(1.75);
    }
  }

  h3 {
    @include relative-font-size(1.625);

    @include media-query($on-laptop) {
      @include relative-font-size(1.375);
    }
  }

  h4 {
    @include relative-font-size(1.25);

    @include media-query($on-laptop) {
      @include relative-font-size(1.125);
    }
  }

  a {
    color: $dark-green;
  }
}

//SEARCH RESULT
#search-container {

  margin-right: 75px;
  border-radius: 5px;
  background-color: $white;


  #search-input {
    width: 310px;
    height: 40px;
    border-radius: 5px;
  }

  #results-container {
    list-style-type: none;
    margin-left: 0px;
    margin-top: 50px;
    width: 310px;
    z-index: 20;
    position: absolute;
    border-radius: 5px;
    background-color: $white;
    li {
      width: 100%;
      a {
        color: $dark-green;
      }
    }
  }
}

#clear {
  position: absolute;
  border-radius: 5px;
  right: 0px;
  z-index: 2;
  border: none;
  height: 40px;
  cursor: pointer;
  color: white;
  background-color: $dark-green;
}

//PAGINATION LINKS
.pagination {

  margin: auto;
  margin-bottom: 30px;
  text-align: center;
  background-color: $text-color;
  color: $white;

  a, em, span {
    border: 2px solid $text-color;
    border-radius: 8px;
    padding: 5px;
    padding-left: 10px;
    padding-right: 10px;
    text-decoration: none;
    font-size: 20px;
  }

  a:hover {
    color: $atom-color;
  }
}
