@charset "utf-8";

@font-face {
  font-family: "ubuntu-regular";
  src: url("../assets/fonts/Ubuntu/Ubuntu-Regular.ttf");
}

// Define defaults for each variable.

$base-font-family:  ubuntu-regular !default;
$base-font-size:   18px !default;
$base-font-weight: 400 !default;
$small-font-size:  $base-font-size * 0.875 !default;
$base-line-height: 1.5 !default;

$spacing-unit:     30px !default;

$text-color:       #2c3e50 !default;
$background-color: #bdc3c7 !default;
$brand-color:      #2a7ae2 !default;
$header-bar-color: #636e72 !default;

$grey-color:       #828282 !default;
$grey-color-light: lighten($grey-color, 40%) !default;
$grey-color-dark:  darken($grey-color, 25%) !default;
$city-light: #dfe6e9 !default;
$mint-leaf: #00b894 !default;
$first-date: #fab1a0 !default;
$soothing-breeze: #b2bec3 !default;
$blue-belize-hole: #2980b9 !default;
$dark-green: #159957;

$table-text-align: left !default;
$table-boder-color: #535c68;
$table-head-color: #576574;
$table-even-row-color: #8395a7;

$tag-h3-color: #2f3640;
$tag-color-hover: black;
$tag-color-backgound: #718093;

$java-color: $tag-color-backgound;
$docker-color: $tag-color-backgound;
$python-color: $tag-color-backgound;
$database-color: $tag-color-backgound;
$jenkins-color: $tag-color-backgound;
$fe-color: $tag-color-backgound;
$others-color: $tag-color-backgound;

$code-blog-backgound-color: #b2bec3;

$white: #ffffff;
// Width of the content area
$content-width:    800px !default;

$on-palm:          600px !default;
$on-laptop:        800px !default;
$border-radius: 15px;
$atom-color: #43a047;
$header-footer-color: #2f3640;

// Use media queries like this:
// @include media-query($on-palm) {
//   .wrapper {
//     padding-right: $spacing-unit / 2;
//     padding-left: $spacing-unit / 2;
//   }
// }
@mixin media-query($device) {
  @media screen and (max-width: $device) {
    @content;
  }
}

@mixin relative-font-size($ratio) {
  font-size: $base-font-size * $ratio;
}

// Import partials.
@import
  "minima/base",
  "minima/layout",
  "minima/syntax-highlighting"
;
